import { useEffect, useState, useContext } from 'react';
import { Routes, Route, Link, useParams } from 'react-router-dom';
import styled from 'styled-components';
import { Nav } from 'react-bootstrap';
import { useDispatch,  } from 'react-redux';
import { addCart } from './../store.js';

// import {Context1} from './../App.js';

let Box = styled.div`
  padding: 20px;
  color: grey;
`;

let YellowBtn = styled.button`
  background: ${props => props.bg};
  color: ${props => props.bg == 'blue'?'white':'black'};
  padding: 10px;
`;

function Detail(props){
  let {id} = useParams();
  let shoesFind = props.shoes.find((x)=>x.id+1 == id);

  let [alert, setAlert] = useState(true);
  let [tab, setTab] = useState(0);
  let [fade, setFade] = useState('');

  useEffect(()=>{
    let a = setTimeout(()=>{
      setAlert(false)
      setFade('end')
    },1000)
    
    return ()=>{
      clearTimeout(a)
      setFade('')
    }
  }, [tab])

  let watched
  useEffect(()=>{
    watched = localStorage.getItem('watched');
    watched = JSON.parse(watched)
    watched.push(id)
    watched = new Set(watched)
    watched = Array.from(watched)
    localStorage.setItem('watched',JSON.stringify(watched))
  },[])

  // let a = useContext(Context1); // state 해체 -> object으로 저장

  let dispatch = useDispatch();

  return (
    <div>
      <div className="container">
        <div>
          {
            alert == true ? <YellowBtn>버튼</YellowBtn> : null
          }
        </div>
        <div className={"row start "+fade}>
          
          <div className="col-md-6">
            <img src={"https://codingapple1.github.io/shop/shoes"+id+".jpg"} width="50%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4>{shoesFind.title}</h4>
            <p>{shoesFind.content}</p>
            <p>{shoesFind.price}</p>
            
            <Link to="/cart" style={{ textDecoration: "none"}}>
              <button className="btn btn-danger" onClick={()=>{
                dispatch(addCart({id:1, title: 'White and Black', count : 1}))
              }}>주문하기</button>
            </Link>
          </div>
        </div>
      </div>
      
      {/* <div>
        <Box>
          <YellowBtn bg="orange">버튼임</YellowBtn>
          <YellowBtn bg="blue">버튼임</YellowBtn>
        </Box>
      </div> */}

      <Nav fill variant="tabs" defaultActiveKey="/blog">
        <Nav.Item>
          <Nav.Link href='/blog' onClick={()=>{setTab(0)}}>Active</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-1" onClick={()=>{setTab(1)}}>Longer NavLink</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="link-2" onClick={()=>{setTab(2)}}>Link</Nav.Link>
        </Nav.Item>
        <Nav.Item>
          <Nav.Link eventKey="disabled" disabled>
            Disabled
          </Nav.Link>
        </Nav.Item>
      </Nav>

      <TabContent tab={tab}/>
    </div>
  )
}

export default Detail;

function TabContent({tab}){
  let [fade, setFade] = useState('');

  useEffect(()=>{
    let a = setTimeout(()=>{setFade('end')},500)
    
    return ()=>{
      clearTimeout(a)
      setFade('')
    }
  }, [tab])

  return (
    <div className={"start "+fade}>
      { [<div>내용0</div>, <div>내용1</div>, <div>내용2</div>][tab] }
    </div>
  )
}